.page-header {
  background-color: $background-header;
}

.page-header__inner {
  padding-top: 15px;
  padding-bottom: 114px;

  background-image: url("../img/bg-header.png");
  background-repeat: no-repeat;
  background-position: right bottom;
}

.page-header__top {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-bottom: 67px;
}

.page-header__phone {
  font-size: 22px;
  line-height: 33px;
  text-decoration: none;
  color: $text-color;
}

.page-header__title {
  width: 796px;
  margin: 0;
  margin-bottom: 77px;

  font-size: 70px;
  font-weight: 800;
  line-height: 84px;
  color: $main-color;
}

.page-header__desc {
  width: 668px;
  margin: 0;
  margin-bottom: 40px;

  font-size: 30px;
  line-height: 41px;
}
