.page-footer {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  padding-bottom: 29px;

  font-size: 18px;
  line-height: 27px;
  color: #a6a6a6;
}

.page-footer__copyright {
  display: inline-block;
  margin: 0;
  margin-right: 60px;
}

.page-footer__privacy {
  padding-bottom: 4px;

  text-decoration: none;
  color: #a6a6a6;
  border-bottom: 1px solid #a6a6a6;
}
