.law {
  padding: 50px 0;

  background-color: $main-color;
  color: #ffffff;
}

.law__row {
  align-items: flex-end;
}

.law__column {
  &--first {
    width: 600px;
  }

  &--last {
    width: 405px;
    margin-bottom: 37px;
    padding-right: 58px;
  }
}

.law__title {
  margin: 0;
}

.law__desc {
  margin: 0;

  font-size: 22px;
  line-height: 33px;
}

.law__appeal {
  position: relative;

  display: block;
  margin-bottom: 20px;
  padding-bottom: 25px;

  font-size: 25px;
  font-weight: 800;
  line-height: 36px;

  &:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;

    background-color: #adb7c6;
  }
}

.law__agent {
  display: flex;
  align-items: center;
  margin: 0;
}

.law__agent-img {
  margin-right: 22px;
}
