.btn {
  display: inline-block;
  margin: 0;
  padding: 8px 28px;
  font: inherit;
  font-size: 15px;
  text-align: center;
  text-decoration: none;
  color: #000000;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}

.btn__quiz {
  padding: 22px 70px 13px 80px;

  font-size: 25px;
  font-weight: 700;
  line-height: 35px;
  color: #ffffff;
  background-color: #ff7a00;
  border-radius: 50px;
}
